import { useState } from 'react';

import apiFetch from '@wordpress/api-fetch';

const useOptions = (name: string) => {
  const [currentOptions, setCurrentOptions] = useState<unknown>(null);

  const getOptions = async () => {
    const options = await apiFetch({
      path: '/static-snap/v1/options/' + name,
    });

    setCurrentOptions(options);

    return options;
  };

  const setOptions = async (options: Record<string, unknown>) => {
    await apiFetch({
      data: options,
      method: 'POST',
      path: '/static-snap/v1/options/' + name,
    });
  };

  return { currentOptions, getOptions, setOptions };
};

export default useOptions;
